/* Lists
–––––––––––––––––––––––––––––––––––––––––––––––––– */
ul {
  list-style: circle inside;
}
ol {
  list-style: decimal inside;
}
ol,
ul {
  padding-left: 0;
  margin-top: 0;
}
ul ul,
ul ol,
ol ol,
ol ul {
  font-size: 100%;
  margin: 1rem 0 1rem 3rem;
  color: var(--text-color-softer);
}
li {
  margin-bottom: 0.5rem;
}
